@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html, body, #root { height: 100%; }
  body { background-color: #0b0b0b; }
}

@layer components {
  .container-max { @apply max-w-6xl mx-auto px-4; }
  .btn-red { @apply bg-accent text-white rounded-lg px-4 py-2 font-medium shadow-red-glow hover:shadow-red-glow-strong transition-all duration-300 ease-in-out; }
  .card { @apply bg-[#111] border border-red-900/20 rounded-2xl p-6 shadow-[inset_0_0_0_1px_rgba(255,255,255,0.05)] hover:shadow-red-glow transition-all duration-300; }
  .muted { @apply text-gray-300; }
}
